.diagram {
  height: 35rem;
  width: 35rem;
  position: relative;

  @media only screen and (orientation: portrait) {
    height: 15rem;
    width: 15rem;
  }

  .circle {
    position: absolute;
    border-radius: 50%;

    &.n1,
    &.n2,
    &.n3 {
      height: 75%;
      width: 75%;
    }

    &.n2,
    &.n3 {
      margin-top: 40%;
    }

    &.n1 {
      right: 10%;
      background: #ff000069;
    }

    &.n2 {
      right: 30%;
      background: #00ff0069;
    }

    &.n3 {
      right: -7%;
      background: #0000ff69;
    }
  }

  .parts {
    font-family: exo;
    position: absolute;

    &.utopia,
    &.fast,
    &.qualitatively,
    &.cheaply {
      font-size: 0.9rem;
    }

    &.expensive,
    &.more-time,
    &.awry {
      font-size: 0.8rem;
    }

    &.qualitatively {
      margin-top: 25%;
      right: 25%;
    }

    &.expensive {
      margin-top: 44%;
      right: 58%;
    }

    &.more-time {
      margin-top: 44%;
      right: 15%;
    }

    &.utopia {
      margin-top: 56%;
      right: 43%;
    }

    &.awry {
      margin-top: 80%;
      right: 34%;
    }

    &.fast {
      margin-top: 75%;
    }

    &.cheaply {
      right: 2%;
      margin-top: 75%;
    }
  }
}

.table-switcher {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-top: 5rem;

  .button {
    margin: 0.5rem 0.1vw 0 0.1vw;
    background: black;
    color: whitesmoke;
    font-family: exo;
    text-align: center;
    width: 18.5vw;
    font-size: 0.7rem;

    &:hover {
      background: darkgrey;

      &:active {
        background: green;
      }
    }
  }
}

.table {
  border: solid 0.15rem black;
  font-family: exo;
  filter: drop-shadow(0rem 0.1rem 0.1rem #151811);
  border-radius: 0.6rem;

  .profit {
    width: max-content;
    color: #150333;
    list-style: none;
  }
}

.star-text {
  font-size: 0.666rem;
}
