header {
  background: linear-gradient(90deg, #acadad, #a2a2a3);
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 2;
  width: 100%;
  height: 7.55rem;

  .company {
    background: #feffff;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-bottom-right-radius: 0.2rem;
    width: 20vw;
    padding: 0.3rem;
    font-family: "Anek Gurmukhi";
    filter: drop-shadow(0.1rem 0.15rem 0.15rem #e8ebe5);

    .logo {
      position: relative;
      height: 3.1rem;
      width: 3.1rem;
      transition: transform 0.3s ease-in-out;

      &:hover {
        transform: scale(1.08);
      }
    }

    .name {
      position: relative;
      margin: 0.1rem 2vw 0 2vw;
      padding: 0;
      font-size: 2.1rem;
      filter: drop-shadow(0rem 0.1rem 0.1rem #9da793);

      .I,
      .C {
        font-size: 1.6rem;
        padding: 0 0.6vw 0 1vw;
      }
    }

    .code {
      margin: 0;
      padding: 0;
      font-size: 0.677rem;
      height: 0.45rem;
      line-height: 0.999rem;
      font-family: exo;
    }
  }

  .activator {
    display: none;

    @media only screen and (orientation: portrait) {
      cursor: pointer;
      display: flex;
      flex-direction: column;
      align-items: center;
      position: absolute;
      top: 2.5rem;
      right: 6vw;
      overflow: hidden;
      background: transparent;
      border: none;
      filter: drop-shadow(0 0 0.1rem white);
    }

    .line {
      background: #0d1e10;
      position: relative;
      height: 0.1rem;
      margin: 0.22rem;
      width: 1.69rem;
      border: solid 0.1rem #0d1e10;
      transform-origin: left center;
    }
  }

  .navigation {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    position: relative;
    align-items: center;
    top: 1rem;
    left: 0.8rem;

    @media only screen and (orientation: portrait) {
      flex-direction: column;
      align-items: flex-end;
      position: absolute;
      top: 7.1rem;
      right: 0;
    }

    .button {
      display: flex;
      flex-direction: column;
      align-items: center;
      font-family: goldman;
      background: none;
      border: none;
      margin: 0.3rem;
      padding: 0.3rem;
      background: linear-gradient(-45deg, #fca849, #fcd683);
      transition: filter 0.2s ease-in-out, scale 0.25s ease-in-out;
      border-radius: 1rem;
      filter: drop-shadow(0rem 0.1rem 0.1rem #2e3030);

      &:hover {
        filter: drop-shadow(0rem 0.1rem 0.1rem #757272);
        scale: 1.05;
      }

      &:active {
        background: white;
      }

      @media only screen and (orientation: portrait) {
        padding: 0.5rem;
        margin: 0.15rem;
      }

      .link {
        text-decoration: none;
        color: #161616;

        .icon,
        .letters {
          padding: 0.2rem;
          border-radius: 0.6rem;
          display: block;

          @media only screen and (orientation: portrait) {
            padding: 0.58rem;
            text-align: start;
          }
        }

        .icon {
          font-size: 1.2rem;
          color: #0d1e10;
          padding: 0.3rem;
          background: radial-gradient(circle, #14f83a, #ffffff7c);

          @media only screen and (orientation: portrait) {
            width: 75vw;
            filter: drop-shadow(0 0 0.2rem #14f83a);
            background: linear-gradient(0deg, #14f83a, #b2fa89f3);
          }
        }

        .letters {
          padding: 0.25rem;
          background: radial-gradient(circle, #fdc775, #f5b658d0);

          @media only screen and (orientation: portrait) {
            width: 70vw;
            background: linear-gradient(90deg, #fdc775, #f5b658d0);
          }
        }
      }
    }
  }
}
