.aida {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: exo;
  font-size: 1rem;
  border-radius: 0.4rem;
  filter: drop-shadow(0rem 0.2rem 0.2rem #676568);

  @media only screen and (orientation: portrait) {
    font-size: 0.8rem;
  }

  .title {
    position: relative;
    font-family: goldman;
    font-size: 2rem;
    text-shadow: -0.06rem -0.06rem #080808, 0.06rem -0.06rem #080808,
      -0.06rem 0.06rem #080808, 0.06rem 0.06rem #080808;

    @media only screen and (orientation: portrait) {
      font-size: 1.5rem;
    }
  }

  .background {
    position: relative;
    filter: drop-shadow(0rem 0.2rem 0.3rem #676568);
    clip-path: polygon(0 0, 100% 0, 100% 65%, 50% 100%, 0 65%);
    padding-bottom: 2.5rem;
    border-radius: 0.5rem;

    .image {
      height: 11rem;
      position: relative;

      @media only screen and (orientation: portrait) {
        height: 9rem;
      }
    }
  }

  .container {
    position: relative;
    padding: 1rem;
    border-radius: 0.4rem;
    display: flex;
    align-items: center;

    &:nth-child(odd) {
      flex-direction: row-reverse;
      text-align: end;
    }

    .art {
      height: 8rem;
      filter: drop-shadow(0rem 0.2rem 0.1rem #f1f5f7);

      @media only screen and (orientation: portrait) {
        height: 7rem;
      }
    }

    &.desire {
      flex-direction: row;
    }

    &.interest,
    &.action {
      text-align: end;
      flex-direction: row-reverse;
    }
  }
}

.article {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: exo;
  font-size: 1rem;
  border-radius: 0.4rem;

  .background {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 100vw;

    .title {
      z-index: 1;
      color: #11151b;
      filter: drop-shadow(0rem 0.1rem 0.1rem #f1f5f7);
      font-size: 1.7rem;
      padding: 0.15rem;
      font-family: goldman;
      text-transform: uppercase;

      @media only screen and (orientation: portrait) {
        font-size: 1rem;
      }
    }

    .image {
      z-index: 0;
      width: 45vw;
      position: absolute;
    }
  }

  .content {
    width: 84vw;

    .intro {
      font-size: 1rem;

      @media only screen and (orientation: portrait) {
        font-size: 0.85rem;
      }
    }
  }

  .container {
    display: flex;
    align-items: center;
    flex-direction: column;

    .sub-title {
      text-align: center;
      border-radius: 1rem;
      padding: 0.2rem;
      font-size: 1.2rem;

      @media only screen and (orientation: portrait) {
        font-size: 0.95rem;
      }
    }

    .sub-text {
      padding: 2rem;

      @media only screen and (orientation: portrait) {
        font-size: 0.8rem;
        padding: 0.8rem;
        margin: 0 2vw 0 2vw;
      }
    }
  }

  .ending {
    padding: 1.2rem;
    font-size: 1rem;

    @media only screen and (orientation: portrait) {
      font-size: 0.85rem;
    }
  }

  &.process {
    @media only screen and (orientation: portrait) {
      margin-top: 3rem;
    }

    .background {
      .title {
        text-align: end;
        width: 60vw;
        padding: 0.5rem 3vw 0.5rem 0;
        margin: 3rem 0 4rem 40vw;
      }

      .image {
        left: 2vw;
      }
    }

    .container {
      align-items: flex-start;

      .sub-title {
        text-align: start;
        width: 80vw;
        margin-left: -4vw;
        clip-path: polygon(
          0 5%,
          60% 5%,
          85% 0,
          100% 50%,
          85% 100%,
          60% 95%,
          0 95%
        );
      }

      .sub-text {
      }
    }

    .ending {
      border-color: #f7f7f7;
      border-top-left-radius: 2rem;
      border-bottom-left-radius: 2rem;
    }
  }

  &.technologies {
    @media only screen and (orientation: portrait) {
      margin-top: 4.5rem;
    }

    .background {
      .title {
        width: 100vw;
        padding: 3rem 0 3rem 0;
        text-align: center;
      }

      .image {
        width: 100vw;
      }
    }

    .content {
      .intro {
        @media only screen and (orientation: portrait) {
          margin-top: 2rem;
          padding: 0.7rem;
        }
      }
    }

    .container {
      .sub-title {
        border-top: solid 0.2rem;
        border-bottom: solid 0.2rem;

        @media only screen and (orientation: portrait) {
          width: 80vw;
        }
      }
    }

    .ending {
      border-top: solid 0.2rem;
      border-bottom: solid 0.2rem;
      border-color: #f7f7f7;
    }
  }

  &.ppc,
  &.seo,
  &.social,
  &.content,
  &.email {
    position: relative;

    .background {
      width: 80vw;

      .title {
      }

      .image {
        position: relative;
        top: 3rem;
        z-index: -1;
      }
    }
    .content {
      .intro {
      }
    }
    .container {
      .sub-title {
      }
      .sub-text {
      }
    }
    .ending {
    }
  }

  &.ppc {
  }

  &.seo {
    .title {
    }
  }

  &.social {
    .title {
    }
  }

  &.email {
    .title {
    }
  }

  &.content {
    .title {
    }
  }

  &.trends {
    margin-top: 1rem;

    .background {
      .title {
        margin: 0 0 2rem 15vw;
        padding: 13rem 15vw 0 6vw;
        clip-path: polygon(100% 0, 0% 100%, 100% 100%);
        background: linear-gradient(90deg, #f59de9, transparent);
      }

      .image {
        right: 3vw;
      }
    }
  }

  &.services {
    .title {
      right: 47vw;
    }
  }

  &.advantages {
    margin-top: 2rem;
    width: 85vw;

    .background {
      .title {
      }
    }

    .container {
      .sub-title {
        padding: 0.25rem;
        width: 55vw;
        background: #dbca9a;
        filter: drop-shadow(0 0.1rem 0.1rem #888b88);
      }
      .sub-text {
        text-align: end;
      }
    }

    .ending {
    }
  }
}

.list {
  border-radius: 0.4rem;
  padding: 2rem 2vw 1rem 2vw;

  .title {
    font-family: "goldman";
    font-size: 2rem;
    text-align: center;
    margin: 2vw 2vw 2rem 0;
  }

  .sub-title {
    text-align: center;
    font-family: "exo";
    text-transform: uppercase;
    font-size: 1.1rem;
    color: #161616;
  }

  .topic-title,
  .benefits-title {
    text-transform: uppercase;
    text-align: start;
    font-family: "goldman";
    font-size: 1rem;
    color: #161616;
    padding: 0.2rem;
    border-radius: 0.3rem;
    background: white;
  }

  .container {
    list-style-type: square;
    font-family: "exo";
    text-align: start;
  }

  .ending {
    padding: 1rem;
    font-family: "exo";
    text-transform: uppercase;
    font-size: 1.2rem;
    text-align: center;
    margin: 2rem 0vw 2rem 0;
    border-radius: 0.3rem;
    filter: drop-shadow(0rem 0.2rem 0.1rem #86837e);
    color: #161616;
    background-color: white;
  }
}

.couple {
  margin: 2rem 2vw 0 2vw;

  .title {
    font-size: 2rem;
    text-align: center;
    font-family: exo;

    @media only screen and (orientation: portrait) {
