    }
  }

  .container {
    padding: 0.3rem;

    .sub-title {
      font-size: 1.6rem;
      border-radius: 0.2rem;
      text-align: center;
      padding: 0.2rem;

      @media only screen and (orientation: portrait) {
        font-size: 1rem;
        letter-spacing: 0.2rem;
      }
    }

    .man,
    .woman {
      padding: 0.15rem;
      margin: 0.2rem;
      border-radius: 0.3rem;
      font-size: 1rem;

      @media only screen and (orientation: portrait) {
        font-size: 0.8rem;
      }
    }

    .first,
    .second {
      font-family: exo;
      padding: 0.3rem;

      @media only screen and (orientation: portrait) {
        font-size: 0.8rem;
      }
    }

    .first {
      .man {
      }
    }

    .second {
      .woman {
      }
    }
  }
  .ending {
    text-align: center;
    padding: 0.5rem;
    font-family: exo;

    @media only screen and (orientation: portrait) {
      font-size: 0.85rem;
    }
  }
}

