footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  position: relative;
  z-index: 1;

  .arrow {
    width: 20%;
    margin: 0;
    padding: 0;
    text-align: center;
    position: relative;
    border-top-left-radius: 50%;
    border-top-right-radius: 50%;
    font-size: 4rem;
    display: block;
    overflow: hidden;
    box-shadow: 0 0 1rem #0000004d;
  }

  .content {
    position: relative;
    text-align: center;
    background: linear-gradient(-45deg, #fca849, #f8e091);
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    font-family: goldman;
    width: 100vw;

    .company {
      margin-bottom: 7rem;
      text-align: start;

      @media only screen and (orientation: portrait) {
        margin-bottom: 5rem;
      }
      .title {
        width: 22vw;
        font-size: 1rem;
        text-transform: uppercase;
        color: #48484e;

        @media only screen and (orientation: portrait) {
          font-size: 0.85rem;
        }
      }

      .text {
        padding: 0.2rem;
        color: #161616;
        filter: drop-shadow(0rem 0.15rem 0.2rem #f3f2f0);

        @media only screen and (orientation: portrait) {
          font-size: 0.8rem;
        }
      }
    }

    .services {
      text-align: center;
      margin-bottom: 2rem;
      display: flex;
      margin-left: 7vw;
      flex-direction: column;
      height: 13rem;

      @media only screen and (orientation: portrait) {
        height: 9rem;
        margin-bottom: 3rem;
      }

      .title {
        text-transform: uppercase;
        font-size: 1rem;
        color: #48484e;

        @media only screen and (orientation: portrait) {
          font-size: 0.85rem;
        }
      }

      .link {
        text-transform: uppercase;
        color: #161616;
        font-size: 1rem;
        padding: 0.2rem 1.5vw;
        text-decoration: none;
        filter: drop-shadow(0rem 0.15rem 0.2rem #f3f2f0);
        transition: filter 0.1s ease-in-out, transform 0.25s ease-in-out;

        @media only screen and (orientation: portrait) {
          font-size: 0.8rem;
        }

        &:hover {
          filter: none;
          background: radial-gradient(circle, #fce4b0da, #f8f297);
          border-radius: 3rem;
          transform: scale(1.05);
        }

        &:active {
          background: white;
        }
      }
    }

    .contact_info {
      margin-top: 6rem;
      margin-right: 5vw;
      text-align: center;

      .title {
        color: #48484e;
      }
      .text {
        color: #0d1e10;
      }
    }

    .copyright {
      position: absolute;
      font-family: goldman;
      bottom: 0;
      width: 100%;
      text-align: center;

      @media only screen and (orientation: portrait) {
        font-size: 0.85rem;
      }

      .image {
        width: 3vw;
        margin: 0.2rem 0.4vw -0.2rem 0.4vw;
      }
    }
  }

  .drawer {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin: 0;
    padding: 0;
    position: fixed;
    left: -10.5vw;
    bottom: 2rem;
    background: #c6c6c6;
    color: rgb(0, 0, 0);
    border-top-right-radius: 50%;
    border-bottom-right-radius: 50%;
    transition: left 0.3s ease;
    overflow: hidden;
    box-shadow: 0 0 1rem rgba(0, 0, 0, 0.3);
    background: linear-gradient(#96a69b, #83d784);
    background-size: 300% 100%;

    &:hover {
      left: 0;

      .open_drawer {
        opacity: 0;
      }
    }

    .buttons {
      display: flex;
      flex-direction: column;

      .button {
        color: #414642;
        border: none;
        border-top-right-radius: 50%;
        border-bottom-right-radius: 50%;
        margin-top: 0.25rem;
        margin-bottom: 0.25rem;
        padding: 0.5rem;
        font-family: exo;
        filter: drop-shadow(0.1rem 0.1rem 0.1rem rgba(167, 158, 158, 0.456));

        &.cookies {
          background: radial-gradient(circle, #ffffff, #e8ca36);
        }

        &.rules {
          background: radial-gradient(circle, #ffffff, #e89e36);
        }

        &.privacy {
          background: radial-gradient(circle, #ffffff, #e84236);
        }

        &:hover {
          scale: 1.1;
          box-shadow: 0 0 0.3rem #343434;
          transition: 0.35s ease-in-out;
        }
      }
    }

    .open_drawer {
      font-size: 1.2rem;
      background: none;
      border: none;
      color: black;
      transform: scale(-1);
      transform: rotate(-90deg);
    }
  }
}
