main {
  margin-top: 10rem;
  display: flex;
  flex-direction: column;
  align-items: center;

  .contact {
    padding: 1rem;
    border-radius: 0.4rem;
    font-family: exo;
    border-top: solid 0.2rem;
    border-bottom: solid 0.2rem;
    border-top-right-radius: 5rem;
    border-bottom-left-radius: 5rem;
    border-top-left-radius: 5rem;
    border-bottom-right-radius: 5rem;
    box-shadow: 0 0.1rem 0.4rem #c2bdb2;

    @media only screen and (orientation: portrait) {
      margin-top: 2rem;
    }

    .title {
      font-family: "exo";
      letter-spacing: 0.1rem;
      font-size: 2rem;
      text-align: center;
      border-top: solid 0.2rem;
      border-top-right-radius: 3rem;
      border-bottom-left-radius: 3rem ;
      border-bottom: solid 0.2rem;
      color: #222221;

      @media only screen and (orientation: portrait) {
        font-size: 1.3rem;
      }
    }

    .text {
      margin-left: 0.5rem;
      padding-right: 30vw;
      font-size: 1rem;
      font-family: "exo";
      color: #0b151f;

      @media only screen and (orientation: portrait) {
        font-size: 0.8rem;
      }
    }

    .button {
      position: absolute;
      right: 25vw;
      margin-top: -8rem;
      border: none;
      cursor: pointer;
      line-height: 3.3rem;
    }

    .icon,
    .letters {
      display: block;
      position: absolute;
      width: 21vw;
      letter-spacing: 0.1rem;
      height: 3.3rem;
      font-family: exo;
      font-size: 1.1rem;
      box-shadow: inset 0.02rem 0.02rem 0.02rem 0rem #ffffff80,
        0.04rem 0.04rem 0.04rem 0rem #0000001a,
        0.06rem 0.06rem 0.06rem 0rem #0000001a;
      text-align: center;
      box-sizing: border-box;
      transition: all 0.3s;

      @media only screen and (orientation: portrait) {
        font-size: 0.95rem;
      }

      .icon {
        font-size: 1.5rem;
        transform: rotateX(90deg);
        transform-origin: 50% 50% -1.53rem;
      }

      .letters {
        transform: rotateX(0deg);
        transform-origin: 50% 50% -1.53rem;
      }

      &:hover {
        .icon {
          box-shadow: inset 0.02rem 0.02rem 0.02rem 0rem #ffffff80,
            0.07rem 0.07rem 0.12rem 0rem #0000001a,
            0.04rem 0.04rem 0.05rem 0rem #0000001a;
          transform: rotateX(0deg);
        }

        .letters {
          box-shadow: inset 0.02rem 0.02rem 0.02rem 0rem #ffffff80,
            0.07rem 0.07rem 0.12rem 0rem #0000001a,
            0.04rem 0.04rem 0.05rem 0rem #0000001a;
          color: transparent;
          transform: rotateX(-90deg);
        }
      }
    }
  }
}

.home {
  .welcome {
    overflow: hidden;
    height: 20rem;
    position: relative;
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    font-family: exo;
    background-color: #000;
    background-image: radial-gradient(
        circle at top right,
        rgba(121, 68, 154, 0.13),
        transparent
      ),
      radial-gradient(circle at 20% 80%, rgba(41, 196, 255, 0.13), transparent);
  }

  .video {
    top: -3rem;
    width: 100vw;
    position: absolute;
  }

  .motto,
  .title {
    position: relative;
    width: 50%;
    color: whitesmoke;
  }

  .motto {
    font-size: 1.2rem;
    padding: 1rem 1vw 1rem 1vw;
    text-align: center;
  }

  .title {
    font-size: 1.4rem;
    padding: 1rem 1vw 1rem 1vw;
    text-align: center;
  }

  .checkout,
  .indicators {
    background: linear-gradient(to bottom, #bebac2, grey);
    margin: 0.2rem;
  }
  .checkout {
    position: relative;
    font-family: exo;
    text-align: center;
    font-size: 2rem;
    line-height: 3rem;
    display: inline-block;
    border-top-right-radius: 2rem;
    padding: 1.7rem;
    color: #000000;
    text-shadow: -0.06rem -0.06rem #f8f7f3, 0.06rem -0.06rem #f8f7f3,
      -0.06rem 0.06rem #f8f7f3, 0.06rem 0.06rem #f8f7f3;
  }

  .indicators {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    overflow: hidden;
    height: 8rem;
    border-bottom-left-radius: 2rem;

    .button {
      cursor: pointer;
      position: relative;
      margin: 0.1rem;
      border-radius: 1rem;

      .background {
        padding: 0.5rem;
        clip-path: circle(45% at 50% 50%);
        height: 6.5rem;
        filter: drop-shadow(0rem 0.1rem 0.1rem #cfcdca);
      }

      .sub-title {
        font-family: goldman;
        padding: 0.85rem 0.85vw 0.85rem 0.85vw;
        background: linear-gradient(-45deg, #fca849, #f8e091);
        border-radius: 3rem;
        border: solid 0.3rem #faf8f2;
        font-size: 0.65rem;
        width: 15vw;
        color: #0d1e10;
        text-align: center;
        filter: drop-shadow(0rem 0.2rem 0.1rem #676867);
        transition: transform 0.4s, color 0.5s, border-color 0.1s ease-in-out;

        &:hover {
          background: #f8f7f3;
          border: solid 0.3rem #f8f7f3;
          transform: translateY(-7%);
          filter: drop-shadow(0rem 0.2rem 0.1rem #4e4b4b);
        }
      }
    }
  }

  .slider-button {
    color: #d6dde6;
    border-radius: 0.2rem;
    height: 3rem;
    position: absolute;
    filter: drop-shadow(0rem 0.2rem 0.1rem #e2e2e0);
  }

  .slide {
    width: 90vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 4rem;
    padding: 1rem;
    transition: transform 0.5s ease-in-out;

    .text {
      text-align: center;
      font-family: exo;
      font-size: 1rem;
      color: #000000;
      margin: 2rem;
      width: 80vw;
    }

    .link {
      position: relative;
      bottom: 1rem;
      background: linear-gradient(to bottom, #bebac2, grey);
      display: flex;
      flex-direction: row;
      align-items: center;
      text-decoration: none;
      transition: filter 0.2s ease-in-out, transform 0.25s ease-in-out;
      border-radius: 1rem;
      filter: drop-shadow(0rem 0.1rem 0.1rem #c5ddd1);

      &:hover {
        transform: scale(1.1);
        background-color: #68c993;
      }

      .to {
        position: relative;
        margin: 1rem 1vw 1rem 2vw;
        color: #000000;
        text-shadow: -0.06rem -0.06rem #f8f7f3, 0.06rem -0.06rem #f8f7f3,
          -0.06rem 0.06rem #f8f7f3, 0.06rem 0.06rem #f8f7f3;
        font-size: 1.1rem;
        overflow: hidden;
        font-family: exo;

        &:hover {
          text-decoration: underline;
        }
      }

      .right {
        margin-right: 1vw;
        color: #188f22;
        font-size: 1.3rem;
      }

      .image {
        margin-right: 2vw;
        width: 10vw;
        border-top-right-radius: 0.4rem;
        border-bottom-left-radius: 0.4rem;
        filter: drop-shadow(0rem 0.2rem 0.3rem #e2e1dc);
      }
    }
  }

  .contact_form {
    font-family: goldman;
    width: 80vw;
    margin-top: 4rem;
    padding: 1.25rem;
    background: linear-gradient(to right, #fdc775, #fcda6a);
    border-radius: 0.4rem;

    .title {
      color: #1d1d1c;
      font-size: 1.5rem;
      text-align: start;
    }

    .form {
      display: flex;
      flex-direction: column;
      width: 60vw;

      .field {
        padding: 0.2rem;
        border-top-left-radius: 0.5rem;
        filter: drop-shadow(0rem -0.02rem 0.1rem #f3f3eb);
        background: radial-gradient(circle, #c1c7c5da, #c0e2d7);
        font-size: 1rem;
        color: #1d1d1c;
        margin-top: 1rem;
      }

      .input {
        position: relative;
        width: 57vw;
        padding: 0.6rem;
        border: none;
        border-bottom-right-radius: 0.5rem;
        font-size: 1rem;
        filter: drop-shadow(0rem 0.1rem 0.1rem #848686);

        &.message {
          width: 57vw;
          height: 10rem;
          resize: none;
        }
      }

      .choice {
        filter: drop-shadow(0rem 0.1rem 0.1rem #848686);
        padding: 0.8rem;
        border-radius: 0.3rem;
        font-size: 1rem;
        width: 60vw;
        font-family: goldman;
        border: none;
      }

      .required {
        color: red;
        font-size: 1.2rem;
        font-style: bold;
      }

      .send {
        border: none;
        color: #0e0d0d;
        background: #fff9f5;
        font-family: goldman;
        padding: 0.92rem 2rem;
        text-align: center;
        text-decoration: none;
        display: inline-block;
        font-size: 1rem;
        margin: 1.7rem;
        border-radius: 0.5rem;
        transition: filter 0.1s ease-in-out, transform 0.25s ease-in-out,
          background-size 0.3s ease-out;

        &:hover {
          background: radial-gradient(circle, #c1c7c5da, #c0e2d7);
          transform: scale(1.05);
        }
      }
    }
  }
}
