.about {
  .info {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: exo;
    border-radius: 0.4rem;
    padding: 1.2rem;
    background: linear-gradient(90deg, #68fd68, #1fb11f);

    .title {
      font-size: 1.1rem;
      margin-right: 27vw;
    }

    .image {
      position: absolute;
      right: 0;
      height: 12rem;
      filter: drop-shadow(0rem 0.1rem 0.1rem #505251);
    }

    .container {
      display: flex;
      flex-direction: column;
      align-items: center;

      .sub-title {
        font-family: goldman;
        text-align: center;
        font-size: 1.1rem;
        background-color: #dfd9cb;
        padding: 0.4rem;
        text-transform: uppercase;
        border-radius: 0.3rem;
        margin-top: 1.5rem;
        filter: drop-shadow(0.1rem 0.1rem 0.2rem #858686);
      }

      .sub-text {
      }

      .quote {
        padding: 0.8rem;
        border-radius: 0.3rem;
        border-left: 0.5rem solid #393a38;
        background-color: #cfd8cf;
      }
    }
  }

  .goals {
    margin-top: 3rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: exo;
    border-radius: 0.4rem;
    padding: 1.2rem;
    background: linear-gradient(90deg, #517efc, #79a6fa);

    .title {
      font-size: 1.1rem;
      margin-left: 27vw;
    }
    .image {
      position: absolute;
      left: 0;
      height: 12rem;
      filter: drop-shadow(0rem 0.1rem 0.1rem #c9cfcc);
    }
    .container {
      .sub-title {
        font-family: goldman;
        text-align: center;
        font-size: 1.1rem;
        background-color: #dfd9cb;
        padding: 0.4rem;
        text-transform: uppercase;
        border-radius: 0.3rem;
        margin-top: 1.5rem;
        filter: drop-shadow(0.1rem 0.1rem 0.2rem #858686);
      }

      .sub-text {
      }
    }
  }

  .opinion {
    margin-top: 1.8rem;
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: center;

    .icon {
      font-size: 3.5rem;
      color: #27241c;
      transform: rotate(90deg);
    }

    .title {
      border-bottom: solid 0.31rem;
      border-bottom-right-radius: 0.8rem;
      border-bottom-left-radius: 0.8rem;
      font-size: 1.5rem;
      margin-top: 1rem;
      margin-block-end: -4rem;
      padding: 1rem;
      font-family: goldman;
      text-align: start;
      color: #464242;
    }

    .container {
      position: relative;
      display: flex;
      height: 20rem;
      width: 80vw;
      flex-direction: column;
      align-items: center;
      background-color: #fdfeff;
      padding: 1rem;
      border: 2px solid #333;
      border-radius: 0.4rem;
      margin-bottom: 1rem;
      box-shadow: 0.1rem 0.3rem 0.3rem 0 #00000033, 0 0.3rem 0.4rem 0 #00000030;

      .name {
        font-family: exo;
        color: #333;
        font-size: 1.3rem;
      }

      .star {
        color: #e4c724;
        font-size: 1.3rem;
      }

      .comment {
        font-family: exo;
        font-size: 1rem;
        text-align: center;
        line-height: 1.6;
      }
    }

    .indicators {
      display: flex;
      justify-content: center;

      .indicator {
        width: 0.6rem;
        height: 0.6rem;
        margin: 0.2rem;
        border-radius: 50%;
        background-color: #ccc;

        &.active {
          background-color: #303330;
        }
      }
    }
  }
}
